@keyframes transformToCross {
    from {
      transform: rotate(0deg);
      background-image: url(../../../images/icon__COLOR_icon-main.svg);
    }
    to {
      transform: rotate(180deg);
      background-image: url(../../../images/Group.svg);
    }
  }
  
  @keyframes transformToBurger {
    from {
      transform: rotate(180deg);
      background-image: url(../../../images/Group.svg);
    }
    to {
      transform: rotate(0deg);
      background-image: url(../../../images/icon__COLOR_icon-main.svg);
    }
  }
  
  .burger-button {
    animation: transformToBurger .3s linear ;
    width: 44px;
    height: 44px;
    background-color: transparent;
    border: none;
    background-image: url(../../../images/icon__COLOR_icon-main.svg);
    background-size: cover;
    background-repeat: no-repeat;
    display: none;
    cursor: pointer;
    z-index: 4;
    position: absolute;
    top:0;
    right: 0;
    margin: 20px;
  }
  
  @media screen and (max-width:768px) {
    .burger-button{
      display: block;
    }
  }
  
  @media screen and (max-width:320px) {
    .burger-button{
      width: 40px;
      height: 43px;
      background-size: 39px;
    }
  }
  
  .burger-button_oppened{
    animation: transformToCross .3s linear;
    width: 44px;
    height: 44px;
    background-color: transparent;
    border: none;
    background-image: url(../../../images/Group.svg);
    background-size: cover;
    background-repeat: no-repeat;
    display: none;
    cursor: pointer;
    z-index: 4;
  }
  
  @media screen and (max-width:768px) {
    .burger-button_oppened{
      display: flex;
    }
  }
  
  @media screen and (max-width:320px) {
    .burger-button_oppened{
      width: 40px;
      height: 43px;
      background-size: 39px;
    }
  }